import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { firebaseapp } from "../../utils";
import "firebase/storage";
import "firebase/database";

const Container = styled.div`
	display: flex;
	padding: 5vw;
	flex-direction: ${props => (props.theme.screens.mmd ? "column" : "row")};
	justify-content: space-around;
	align-items: center;
	background: url("/images/all_circles.svg");
`;

const TextContainer = styled.div`
	padding: 5vw;
`;

const Img = styled.img`
	width: ${props => (props.theme.screens.mmd ? "90%" : "50vw")};
	padding-bottom: ${props => (props.theme.screens.mmd ? "8vw" : "0vw")};
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	color: ${props => props.theme.colors.white};
	font-weight: 700;
	font-size: 3rem;
`;

const SubText = styled.p`
	font-family: "Crimson Text", serif;
	font-weight: 300;
	font-size: 1.5rem;
	color: ${props => props.theme.colors.white};
`;

const Home: React.FC = () => {
	const [img, setImg] = useState("");
	const [text, setText] = useState("");

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("interior_decorations/modular_kitchens/10.jpg")
			.getDownloadURL()
			.then(url => setImg(url));
	}, []);

	useEffect(() => {
		firebaseapp
			.database()
			.ref("interior_decorations/Description")
			.once("value")
			.then(snapshot => setText(snapshot.val()));
	});

	const getHTML = () => {
		return { __html: text };
	};

	return (
		<Container>
			<TextContainer>
				<Heading>Interior Decorations</Heading>
				<SubText dangerouslySetInnerHTML={getHTML()}></SubText>
				<br></br>
			</TextContainer>
			<Img src={img}></Img>
		</Container>
	);
};

export default Home;
