import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Card } from "react-bootstrap";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Pagination, Autoplay } from "swiper";

import "swiper/swiper.scss";
import "swiper/components/pagination/pagination.scss";

SwiperCore.use([Pagination, Autoplay]);

import { useTheme } from "../../styles";
import { firebaseapp } from "../../utils";
import "firebase/database";

const Container = styled.div`
	display: flex;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	flex-wrap: wrap;
	background-image: url("/images/feedbackBG.svg");
	background-position: center;
	padding: 7vw;
`;

const Head = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 500;
	padding: ${props => (props.theme.screens.md ? "0 0 2rem 0" : "0 10vw 0 0")};
`;

const Feedbacks: React.FC = () => {
	const { screens, colors } = useTheme();

	const [feedbacks, setFeedbacks] = useState([]);

	useEffect(() => {
		firebaseapp
			.database()
			.ref("interior_decorations/Feedbacks")
			.once("value")
			.then(snapshot => {
				setFeedbacks(snapshot.val());
			});
	}, []);

	return (
		<Container>
			<Head>
				Here's what our <br></br>customers say<br></br> about us
			</Head>

			<Swiper
				spaceBetween={60}
				slidesPerView={screens.lg ? 1 : 3}
				style={{ flex: 1, width: "100%" }}
				direction="horizontal"
				pagination={{ clickable: true }}
				autoplay={{
					delay: 1500,
					disableOnInteraction: false,
				}}
			>
				{feedbacks.map(f => (
					<SwiperSlide>
						<Card
							style={{
								backgroundColor: `${colors.darkBlue}`,
								color: `${colors.white}`,
								fontFamily: "Open Sans, sans-serif",
							}}
						>
							<Card.Body>"{f.text}"</Card.Body>
							<Card.Footer>{f.author}</Card.Footer>
						</Card>
					</SwiperSlide>
				))}
			</Swiper>
		</Container>
	);
};

export default Feedbacks;
